.site-header {
  display: none;
  position: relative;
  height: 70px;
}
.site-header .wp-block-group {
  padding: 0 !important;
}

.carousel-item {
  position: relative;
  height: 780px;
  width: 100%;
}
.carousel-item .carousel-image-wrapper {
  position: absolute;
}
.carousel-item .carousel-content {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  top: 50px;
  left: 50px;
  z-index: 1;
}
.carousel-item .carousel-content h2 {
  font-size: 60px;
  text-transform: uppercase;
  --tw-text-opacity: 1;
  color: #000;
  font-family: "clan_procond_news";
  margin-bottom: 0;
}
.carousel-item .carousel-content h4 {
  font-size: 24px;
  font-weight: 500;
  font-family: "clan_procond_news_italic";
  margin-top: 0;
}
.carousel-item .carousel-content p {
  font-size: 20px;
  line-height: 31px;
  margin-bottom: 2.5rem;
}
.carousel-item .carousel-content .cta {
  margin-top: 20px;
  position: relative;
  line-height: 31px;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 16px;
  margin-top: 1rem;
  color: #000;
  background-color: #fff;
  border: 1px solid #000;
  padding: 15px;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
}
.carousel-item .carousel-content .cta:hover {
  background-color: #000;
  color: #fff;
}

.slick-dots {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 1rem 0;
  list-style-type: none;
}
.slick-dots li {
  margin: 0 0.25rem;
}
.slick-dots li button {
  background: #f1f1f1;
}
.slick-dots button {
  display: block;
  width: 1rem;
  height: 1rem;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: #f1f1fe;
  text-indent: -9999px;
}
.slick-dots li.slick-active button {
  background-color: #3c3c3c;
}